.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 0.5s, opacity 0.5s linear;
  z-index: 1000;
}

.modal__container {
  position: relative;
  display: flex;
  background-color: #ffffff;
  border-radius: 10px;
  justify-content: center;
}
.modal__popup-image {
  width: 75vw;
  height: 75vh;
}
.modal_open {
  display: flex;
  visibility: visible;
  opacity: 1;
  transition-delay: 0s;
}

.modal__container_type_preview {
  display: flex;
  flex-direction: column;
}

.modal_type_preview .modal__container {
  background-color: transparent;
  position: relative;
  margin: 0 auto;
}

.modal_type_tooltip {
  max-width: 430px;
  height: 330px;
  background: white;
  border-radius: 10px;
  margin: 0 auto;
  position: absolute;
  left: 0;
  right: 0;
  top: 60px;
  text-align: center;
}

.modal__container_type_tooltip {
  display: flex;
  flex-direction: column;
}

.form__title_type_delete-card {
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  width: 358px;
}
.modal__popup-name {
  font-size: 12px;
  font-weight: 400;
  line-height: 14.52px;
  color: white;
  width: 291px;
}

.modal__close-btn {
  position: absolute;
  top: -40px;
  right: -35px;
  background-color: transparent;
  background-image: url("../images/CloseIcon(1).svg");
  background-repeat: no-repeat;
  background-position: center;
  height: 40px;
  width: 40px;
  border: none;
}

.modal__close-btn:hover {
  opacity: 0.6;
  cursor: pointer;
}

.modal__close-btn_type_tooltip {
  top: -84px;
  right: -60px;
}

@media screen and (max-width: 530px) {
  .modal__close-btn {
    width: 26px;
    height: 26px;
    top: -40px;
    right: 0;
  }

  .modal_type_tooltip {
    max-width: 280px;
    height: 308px;
  }
}
